<div class="flex justify-center" id="app"><div class="container flex column"><div class="filter flex align-center justify-center gap-4 wrap p-4"><span>2025 總預算提案</span><div class="selectors flex align-center gap-4"><span>以</span><el-select v-model="filterType" style="width: 120px" @change="resetFilter"><el-option v-for="item in filterTypes" :key="item.value" :label="item.label" :value="item.value"></el-option></el-select><span>篩選</span><template v-if="filterType === 'legislator'"><el-select v-model="filterValue" placeholder="全部" style="width: 240px" clearable filterable @change="page = 1"><el-option v-for="item in legislators" :key="item.value" :label="item.label" :value="item.value"></el-option></el-select></template><template v-if="filterType === 'party'"><el-select v-model="filterParty" placeholder="全部" style="width: 200px" clearable filterable @change="page = 1"><el-option value="民主進步黨" label="民主進步黨"></el-option><el-option value="中國國民黨" label="中國國民黨"></el-option><el-option value="台灣民眾黨" label="台灣民眾黨"></el-option></el-select></template><template v-if="filterType === 'keyword'"><el-input v-model="filterValue" placeholder="關鍵字" style="width: 200px" clearable @change="page = 1"></el-input></template></div><el-button @click="copyLink">分享搜尋結果</el-button></div><div class="content flex column hack-table gap-8"><el-pagination :total="filteredProposals.length" :page-size="limit" size="large" :pager-count="pageCount" :layout="paginationLayout" :current-page="page" @current-change="page = $event" @size-change="limit = $event"></el-pagination><el-table ref="table" :data="pagedProposals" stripe border><el-table-column type="expand" label="&gt;" width="30"><template #default="scope"><div class="proposal-content-container flex column p-20 gap-8"><div class="date">{{ scope.row.date }}</div><div class="phase">{{ scope.row.phase }}</div><div class="proposal-content">{{ scope.row.content }}</div></div></template></el-table-column><el-table-column label="提案人" :width="proposalWidth"><template #default="scope"><div class="proposers flex column gap-4"><div class="proposer font-font-bold" v-for="proposer in scope.row.proposer">{{ proposer }}</div></div></template></el-table-column><el-table-column label="連署人" :width="proposalWidth" v-if="filterType !== 'party'"><template #default="scope"><div class="co-signers flex column gap-4"><div class="co-signer" v-for="co_signer in scope.row.co_signer">{{ co_signer }}</div></div></template></el-table-column><el-table-column label="單位" prop="unit" width="80"></el-table-column><el-table-column label="類型" width="60"><template #default="scope"><div class="types flex column gap-4"><template v-for="type in scope.row.types"><el-tag type="warning" v-if="type === '凍結'">{{ type }}</el-tag><el-tag type="danger" v-else-if="type === '減列'">{{ type }}</el-tag><el-tag type="info" v-else>{{ type }}</el-tag></template></div></template></el-table-column><el-table-column label="結果" prop="result" width="80"></el-table-column><el-table-column v-if="!phoneMode" label="提案" prop="brief" min-width="350"></el-table-column></el-table></div></div></div>